<template>
  <div class="content" style="background-color: #eee;">
    <!--搜索-->
    <div class="top_header">
      <div class="search">
        <img src="../assets/images/icon_search.png">
        <input type="text" placeholder="输入商家或商品名称"/>
      </div>
    </div>
    <!--tab切换-->
    <div class="tab">
      <div class="order_loan" @click="switchTab(2)">
        <div id="tab_loan" :class="{tab_selected : currentSelected == 2}">赊购订单</div>
        <img :src="currentSelected == 2 ? '../static/images/order_tab_select.png' : '../static/images/order_tab_no_select.png'"/>
      </div>
      <div class="order_money" @click="switchTab(3)">
        <div id="tab_money" :class="{tab_selected : currentSelected == 3}">全款订单</div>
        <img  :src="currentSelected == 3 ? '../static/images/order_tab_select.png' : '../static/images/order_tab_no_select.png'"/>
      </div>
    </div>
    <!--筛选-->
    <div class="select">
        <div class="state" @click="isState = true">
          <span v-text="orderStateIndex.toString() == '' ? '状态' : orderStatusList[orderStateIndex]" class="screen_text"></span>
          <img src="../assets/images/icon_down.png">
        </div>
        <div class="begin_time" @click="isStartTime = true">
          <span v-text="startTime" class="screen_text"></span>
          <img src="../assets/images/icon_down.png">
        </div>
        <div class="end_time" @click="isEndTime = true">
          <span v-text="endTime" class="screen_text"></span>
          <img src="../assets/images/icon_down.png">
        </div>
      </div>
    <!--筛选状态-->
    <van-popup v-model.trim="isState" position="bottom" >
      <van-picker
        show-toolbar
        :columns="orderStatusList"
        @confirm="stateConfirm"
        @cancel="isState = false"
      />
    </van-popup>
    <!--选择开始时间-->
    <van-popup v-model.trim="isStartTime" position="bottom">
      <van-datetime-picker type="datetime" :formatter="formatter"  @confirm="startTimeConfirm" @cancel="isStartTime = false" />
    </van-popup>
    <!--选择结束时间-->
    <van-popup v-model.trim="isEndTime" position="bottom">
      <van-datetime-picker type="datetime" :formatter="formatter" @confirm="endTimeConfirm" @cancel="isEndTime = false" />
    </van-popup>
    <!--订单列表-->
    <div v-for="(info,index) in orderList" class="item" @click="checkOrderInfo(info.order_id)">
      <div class="item-title">
        <span class="order_no">订单编号：{{ info.order_no }}</span>
        <span class="oreder_state">{{ orderStatusList[info.order_status] }}</span>
      </div>
      <ul class="order_info">
        <li id="order_name">姓名：{{ info.consignee }}</li>
        <li id="order_account">收货手机号：{{ info.consignee_mbl }}</li>
        <li id="order_time">下单时间：{{ info.order_add_time }}</li>
        <li id="order_money">实收金额：￥{{ info.order_fat_pay_amount }}</li>
      </ul>
    </div>
    <p class="bar_tite">没有更多啦~</p>
    <div style="height: 2rem;"></div>
  </div>
</template>
<script>
  import * as API from '../service/API';
  import Toast from '../widget/Toast';
  import Loading from '../widget/loading/loading'
  import DateUtils from '../utils/DateUtils';

  export default {
      data() {
        return {
          currentSelected: 2, //2 赊购，3全款
          orderList: [],
          orderStatusList: [],   //订单状态列表
          orderStateIndex:'', //选中订单的状态
          startTime: '开始时间',
          endTime: '结束时间',
          isState: false,     // 状态
          isStartTime: false, // 开始时间
          isEndTime: false, // 结束时间
          startTimeData: '',   //  开始时间选择
          endTimeData: '',   //  结束时间选择
        };
      },
      mounted() {
        let screenHeigt = window.screen.availHeight;
        document.getElementsByClassName('content')[0].style.minHeight = screenHeigt + 'px';
        document.getElementsByClassName('content')[0].style.backgroundColor = '#eee';
      },
      methods: {
        getOrders() {
          let loading = new Loading();
          loading.show();
          let request_url = API.ORDER_LIST + `?order_pay_type=${this.currentSelected}&order_status=${this.orderStateIndex}&o_start=${this.startTimeData}&o_end=${this.endTimeData}`;
          this.$get(request_url).then((response) => {
            if (response.code != 200) {
              new Toast(response.msg).show();
              return;
            }
            this.orderList = response.orderList;
            this.orderStatusList = response.order_status;
            loading.close();
          }).then((error)=>{
            loading.close();
          });
        },
        switchTab(tab) {
          this.currentSelected = tab;
          this.getOrders();
        },
        checkOrderInfo(id) {
          this.$router.push({path: `/orderDetails/${id}`});
        },
        //状态确定
        stateConfirm(value, index) {
          this.orderStateIndex = index;
          this.isState = false;
          this.getOrders();
        },
        //开始时间确认
        startTimeConfirm(val){
          this.isStartTime = false;
          this.startTimeData = new DateUtils().formatDate(val,'yyyy-MM-dd HH:mm:ss');
          this.startTime = new DateUtils().formatDate(val,'yy-MM-dd HH:mm:ss');
          this.getOrders();
        },
        //结束时间确认
        endTimeConfirm(val){
          this.isEndTime = false;
          this.endTimeData = new DateUtils().formatDate(val,'yyyy-MM-dd HH:mm:ss');
          this.endTime = new DateUtils().formatDate(val,'yy-MM-dd HH:mm:ss');
          this.getOrders();
        },
        //    选择数据
        formatter(type, value) {
          if (type === 'year') {
            return `${value}年`;
          } else if (type === 'month') {
            return `${value}月`
          } else if (type === 'day') {
            return `${value}日`
          } else if (type === 'hour') {
            return `${value}时`
          } else if (type === 'minute') {
            return `${value}分`
          }
          return value;
        },
      },
      mounted() {
        this.getOrders();
      }
    }
</script>
<style lang="scss" scoped>
  @import "../style/common";
  /********header*********/
  .top_header{
    width: 100%;
    position: fixed;
    left: 0;
    top:0;
    overflow: hidden;
    height: 1.9rem;
    background-color: white;
    border: none!important;
  }

  .top_header .search{
    line-height: 0.78rem;
    margin: 0.54rem 0.5rem 0.54rem 0.5rem;
    background-color: #eeeeee;
    border-radius: 0.39rem;
  }

  .top_header .search img{
    padding-left: 0.5rem;
    padding-bottom: 0.1rem;
    width: 0.48rem;
    height: 0.48rem;
  }

  .top_header .search input {
    border: none;
    outline: none;
    background-color: transparent;
    font-size: 0.4rem;
    vertical-align: middle;
  }

  /**tab**/
  .tab{
    margin-top: 1.9rem;
    padding-bottom: 1px;
    background: white;
    display: flex;
    justify-content: space-around;
    font-size: 0.4rem;
    height: 0.98rem;
    align-items: center;
    border-bottom: 1px solid #eeeeee;
  }

  .tab_selected{
    color: #47bb74;
  }

  .tab img{
    padding-top: .34rem;
    width: 1.64rem;
  }

  /*******select*******/
  .select{
    height: 1.12rem;
    padding-bottom: .34rem;
    background-color: white;
    display: flex;
  }

  .select span{
    color: #999999;
    font-size: .4rem;
  }

  .select img{
    padding-top: .06rem;
    width: .25rem;
    height: .13rem;
    margin-left: .15rem;
  }

  .select .state{
    margin-left: .4rem;
    font-size: 0;
    line-height: 1.46rem;
  }

  .select .begin_time,.end_time{
    margin-left: .55rem;
    font-size: 0;
    line-height: 1.46rem;
  }

  /********item*********/
  .item{
    margin-top: 0.19rem;
    background-color: white;
  }

  .item .item-title{
    margin: 0 0.5rem;
    padding: 0.37rem 0;
    color: #999999;
    font-size: 0.36rem;
    border-bottom: #eeeeee 1px solid;
    display: flex;
    justify-content: space-between;
  }

  .item .item-title .oreder_state{
    color: #db6045;
  }

  .item .order_info{
    margin: 0.18rem 0.5rem 0.28rem 0.5rem;
    padding-bottom: 0.28rem;
    color: $font_100;
    font-size: 0.4rem;
    border-bottom: 1px solid #eeeeee;
  }

  .item ul li{
    margin-top: 0.3rem;
  }

  .item .order_goods #goods_name{
    color: $font_100;
    font-size: 0.4rem;
    margin-left: 0.5rem;
  }

  .item .order_goods #goods_info{
    width: 100%;
    color: #999999;
    font-size: 0.4rem;
    margin-left: 0.5rem;
    margin-top: 0.22rem;
    padding-bottom: 0.49rem;
  }

  .item .order_goods #goods_info span{
    display: inline-block;
  }

  .item .order_goods #goods_info #goods_type{
    width: 65%;
  }

  .item .order_goods #goods_info #goods_money {
    float: right;
    padding-right: 1rem;
    color: $font_100;
  }

  /*******筛选span*****/
  .screen_text{
    display: inline-block;
    max-width: 3.5rem;
    overflow:hidden;
    text-overflow:ellipsis;
    white-space:nowrap;
    vertical-align: middle;
  }
  /*  提示  */
  .bar_tite{
    font-size: .32rem;
    text-align: center;
    color: #989898;
    margin: .2rem 0 .5rem;
  }
</style>



